/* Order History */
.history-container {
    padding: 2rem 0;
  }
  
  .history-title {
    font-size: 2rem;
    margin-bottom: 2rem;
    color: var(--dark);
  }
  
  .order-card {
    background: white;
    border-radius: 10px;
    padding: 1.5rem;
    margin-bottom: 1.5rem;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  }
  
  .order-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid #eee;
  }
  
  .order-id {
    font-weight: 700;
    color: var(--primary);
  }
  
  .order-date {
    color: var(--gray);
  }
  
  .order-items {
    margin-bottom: 1rem;
  }
  
  .order-item {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
    border-bottom: 1px solid #f5f5f5;
  }
  
  .order-total {
    text-align: right;
    font-weight: 700;
    font-size: 1.1rem;
    color: var(--dark);
  }
  
  .no-orders {
    text-align: center;
    padding: 3rem;
    color: var(--gray);
  }