/* Reset e estilos globais */
:root {
  --primary: #4361ee;
  --primary-dark: #3a56d4;
  --secondary: #3f37c9;
  --dark: #1a1a2e;
  --light: #f8f9fa;
  --success: #4cc9f0;
  --danger: #f72585;
  --warning: #f8961e;
  --gray: #6c757d;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.6;
  background-color: lightgray;
  color: #333;
}

#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

a {
  text-decoration: none;
  color: inherit;
  
}

button {
  cursor: pointer;
  border: none;
  transition: all 0.3s ease;
}

/* Container principal */
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  flex: 1;
}