/* Cart */
.cart-container {
    background: white;
    border-radius: 10px;
    padding: 2rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    margin: 2rem 0;
  }
  
  .cart-title {
    font-size: 1.75rem;
    margin-bottom: 1.5rem;
    color: var(--dark);
    border-bottom: 2px solid #eee;
    padding-bottom: 0.5rem;
  }
  
  .cart-items {
    margin-bottom: 2rem;
  }
  
  .cart-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 0;
    border-bottom: 1px solid #eee;
  }
  
  .item-info {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .item-image {
    width: 60px;
    height: 60px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  .item-details h4 {
    margin-bottom: 0.25rem;
  }
  
  .item-quantity {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .quantity-btn {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #f0f0f0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
  }
  
  .remove-btn {
    background: none;
    color: var(--danger);
    font-weight: 600;
  }
  
  .cart-total {
    font-size: 1.25rem;
    font-weight: 700;
    text-align: right;
    margin-top: 1rem;
  }
  
  .checkout-btn {
    width: 100%;
    padding: 1rem;
    background-color: var(--success);
    color: white;
    font-weight: 600;
    border-radius: 5px;
    margin-top: 1.5rem;
  }
  
  .checkout-btn:hover {
    background-color: #3ab9df;
  }
  
  .empty-cart {
    text-align: center;
    padding: 2rem;
    color: var(--gray);
  }